<template>
  <div class="basic-slot">
    <div class="demo-item-group">
      <label>1. , </label>
      <div class="demo-item">
        <localCompt1>
          <h3>I am </h3>
          <p>I have no use </p>
        </localCompt1>
      </div>
      <div class="demo-item">
        <localCompt1></localCompt1>
      </div>
    </div>
    <div class="demo-item-group">
      <label>2. </label>
      <div class="demo-item">
        <local-compt-2>
          <template v-slot:slot1>
            <p>I am  in slot1</p>
          </template>
          <p>2.6+</p>
          <p>I am 2 in default slot</p>
          <template v-slot:slot2>
            <p>I am  in slot2</p>
          </template>
        </local-compt-2>
      </div>
      <div class="demo-item">
        <local-compt-2>
          <template slot="slot1">
            <p>I am  in slot1</p>
          </template>
          <template slot="default">
            <p></p>
            <p>I am 2 in default slot</p>
          </template>
          <template slot="slot2">
            <p>I am  in slot2</p>
          </template>
        </local-compt-2>
      </div>
      <div class="demo-item">
        <local-compt-2>
          <template #slot1>
            <p>I am  in slot1</p>
          </template>
          <template #default>
            <p>,</p>
            <p>I am 2 in default slot</p>
          </template>
          <template #slot2>
            <p>I am  in slot2</p>
          </template>
        </local-compt-2>
      </div>
    </div>
    <div class="demo-item-group">
      <label>3. </label>
      <div class="demo-item">
        <p>I am 1 in default slot</p>
        <p></p>
        <!--localCompt2>
          <template v-slot:[slotName1]>
            <p>I am  in slot1</p>
          </template>
          <template v-slot:default>
            <p>,</p>
            <p>I am 2 in default slot</p>
          </template>
          <template v-slot:[slotName2]>
            <p>I am  in slot2</p>
          </template>
        </localCompt2>-->
      </div>
    </div>
    <div class="demo-item-group">
      <label>4. </label>
      <p>slotfunction(option) {return ...}, optionprop, 
        slotv-bindaaa="value"option={aaa}templatev-slot:slot1="slotProp"slotProp</p>
      <div class="demo-item">
        <local-compt3>
          <template v-slot:slot1="slotProp">
            <p>{{title}}</p>
            <p>slot1: {{slotProp.msg}}, slotProp={prop}</p>
          </template>
          <template #default="{aha: Aha}">
            <p>2.6+</p>
            <p>default: {{Aha}}, </p>
          </template>
          <template #slot2="{slotColor2}">
            <p>slot2: , solt1: {{slotColor2.color1}}; default: {{slotColor2.color2}}; slot2: {{slotColor2.color3}}</p>
          </template>
        </local-compt3>
      </div>
      <div class="demo-item">
        <local-compt3>
          <template slot="slot1" slot-scope="slotProp">
            <p>{{title}}</p>
            <p>slot1: {{slotProp.msg}}, slotProp={prop}</p>
          </template>
          <template slot-scope="{aha: Aha}">
            <p></p>
            <p>default: {{Aha}}, </p>
          </template>
          <template slot="slot2" slot-scope="{slotColor2}">
            <p>slot2: , solt1: {{slotColor2.color1}}; default: {{slotColor2.color2}}; slot2: {{slotColor2.color3}}</p>
          </template>
        </local-compt3>
      </div>
    </div>
    <div class="demo-item-group">
      <label>5. , v-slottemplate</label>
      <div class="demo-item">
        <local-compt3>
          <template #default="{aha: Aha}">
            <p>default: {{Aha}}, </p>
          </template>
        </local-compt3>
        <local-compt3 v-slot:default="{aha: Aha}">
          <p>default: {{Aha}}, </p>
        </local-compt3>
        <local-compt3 v-slot="{aha: Aha}">
          <p>default: {{Aha}}, </p>
        </local-compt3>
      </div>
    </div>
  </div>
</template>

<script>
var localCompt1 = {
  data () {
    return {
      name: '1',
      styleObj: {
        'color': '#ff9933'
      },
      title: 'hello !'
    }
  },
  template: `<div><h3>{{name}}</h3>
             <div :style="styleObj"><slot>{{title}}</slot></div>
            </div>`
}
var localCompt2 = {
  data () {
    return {
      name: '2',
      styleObj: {
        color1: '#ff9933',
        color2: '#00e64d',
        color3: '#0059b3'
      },
      title: 'hello !'
    }
  },
  template: `<div><h3>{{name}}</h3>
             <div :style="{color: styleObj.color1}"><slot name="slot1"></slot></div>
             <div :style="{color: styleObj.color2}"><slot></slot></div>
             <div :style="{color: styleObj.color3}"><slot name="slot2"></slot></div>
            </div>`
}
var localCompt3 = {
  data () {
    return {
      name: '3',
      info: {name: 'Jack', age: 18},
      message: 'i am local component3',
      aha: 'ahaha',
      styleObj: {
        color1: '#ff9933',
        color2: '#00e64d',
        color3: '#0059b3'
      }
    }
  },
  template: `<div><h3>{{name}}</h3>
             <div :style="{color: styleObj.color1}"><slot name="slot1" v-bind:msg="message"></slot></div>
             <div :style="{color: styleObj.color2}"><slot v-bind:aha="aha"></slot></div>
             <div :style="{color: styleObj.color3}"><slot name="slot2" v-bind:slotColor2="styleObj"></slot></div>
            </div>`
}

export default {
  name: 'basic-slot',
  data: function () {
    return {
      message: 'hello slot',
      slotName1: 'slot1',
      slotName2: 'slot2',
      title: 'hello  !'
    }
  },
  components: {localCompt1, localCompt2, localCompt3}
}
</script>

<style scoped>
  .demo-item-group {
    display: flex;
    margin: 10px 30px;
    flex-direction: column;
    border-bottom: solid 1px #00cc99;
  }
  .demo-item-group > label {
    font-size: 22px;
    color: #000080;
    margin-bottom: 5px;
    font-weight: 600;
  }
  .demo-item-group > label:after {
    content: ":";
  }
  .demo-item-group > .demo-item {
    width: inherit;
    margin-bottom: 5px;
  }
  .demo-item-group > .demo-item:not(:last-of-type) {
    border-bottom: dashed 1px #999;
  }
</style>



