<template>
  <div class="mavonEditor">
    <h5>mavonEditor-MarkDown</h5>
    <!--:ishljs-->
    <!--:v-model Markdownmarkedhtml-->
    <!--change-->
    <!--imgAdd-->
    <!--imgDel-->
    <mavon-editor
      ref="md"
      style="height: 100%"
      :ishljs="true"
      :boxShadow="false"
      :previewBackground="'#fff'"
      v-model="value"
      @change="chang"
      @imgAdd="imgAdd"
      @imgDel="imgDel"
    ></mavon-editor>
    <vc-button @click="uploadImg"></vc-button>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor';
import marked from 'marked';
export default {
  name: 'mavonEditorDemo',
  data() {
    return {
      value: '',
      markedValue: '', // html,
      imgList: [],
      img_file: {}
    }
  },
  components: {
    mavonEditor,
  },
  methods: {
     chang: function (val,render) {
      //html
      // console.log('****html***');
      // console.log(val);
      // console.log(render);
      // console.log('****************');
      this.markedValue=marked(this.value)
       // console.log(this.value);
      // console.log(this.markedValue);
    },
        // @imgAdd event
    imgAdd (pos, file) {
      // 
      this.imgList[pos] = file;
      // // console.log(pos)
      // console.log('' + this.imgList[pos]);
      // .
/*       var formdata = new FormData();
      formdata.append('image', $file);
      this.$http({
          url: '/api/edit/uploadimg',
          method: 'post',
          data: formdata,
          headers: { 'Content-Type': 'multipart/form-data' },
      }).then((res) => {
          let _res = res.data;
          // .url![...](0) -> ![...](url)
          this.$refs.md.$img2Url(pos, _res.url);
      }) */
    },
    imgDel (pos) {
      delete this.imgList[pos];
      // console.log('' + this.imgList[pos]);
    },
    uploadImg () {
      // ..
      for (var i in this.imgList) {
        // console.log('' + this.imgList[i]);
        //
        //multipart/form-data
        var formdata = new FormData();
        formdata.append('file', this.imgList[i]);

        this.$axios({
          url: '',
          method: 'post',
          data: formdata,
          headers: { 'Content-Type': 'multipart/form-data' }
        }).then(res => {
          // .url![...](./0) -> ![...](url)
          /**
           * $vm mavonEditor
           * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
           * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
           */
          // console.log(res);
           //pos0url
            this.$refs.md.$img2Url(pos,url)
        });
      }
    }
  }
}

/* //
import 'mavon-editor/dist/css/index.css';
//div
<div  class='markdown-body'   v-html='markdown_code'/>
//newDate[0].contentmd
this.markdown_code=marked(newData[0].content||,{
   sanitize:true
})
*/
</script>

<style scoped>
.mavonEditor {
  width: 100%;
  height: 500px;
}
</style>



