<template>
  <div class="basicExtend">
    <div class="form-group">
      <label>startMix</label>
      <div class="input-item">
        <button @click.stop="startMix()">startMix</button>
      </div>
    </div>
    <div class="form-group">
      <label>hello</label>
      <div class="input-item">
        <button @click.stop="hello()">hellos</button>
      </div>
    </div>
    <div class="form-group">
      <label>getMsg</label>
      <div class="input-item">
        <p>getMsg: {{getMsg}}</p>
        <p>getMsg2: {{getMsg2}}</p>
        <p>getMsg3: {{getMsg3}}</p>
        <p>getMsg4: {{getMsg4}}</p>
        <p>getMsg5: {{getMsg5}}</p>
      </div>
    </div>
    <div class="form-group">
      <label>Filter</label>
      <div class="input-item">
        <p>{{msg | capitalize2 | capitalize3 | globalFilter}}</p>
      </div>
    </div>
    <p>1. created --> [1, -> 2] --> </p>
    <p>2. datacreated > 2 > 1 >  </p>
    <p>3.  methodscomputed, components, filters  directives, created,</p>
    <p>4. ""</p>
    <p>5. watch</p>
    <p>6. created</p>
    <p>7. VueVue.config.optionMergeStrategies.methods
    VueVue.config.optionMergeStrategies.newOption = {...} 
      Vue.config.optionMergeStrategies.newOption = Vue.config.optionMergeStrategies.methods</p>
    <p>8.  -perant -> -child</p>
    <p>{{showData}}</p>
  </div>
</template>

<script>
import Vue from 'vue'

Vue.filter('globalFilter', function (value) {
  if (!value) return ''
  return value + ' global'
})

Vue.mixin({
  data () {
    return {
      msg: '-msg',
      msg1: '1-msg-1',
      msg2: '1-msg-2',
      msg4: '1-msg4',
      msg5: '1-msg5',
      site: {
        name: '',
        url: 'www.tenant.com',
        hahaList: [3, 4],
        city: 'shenzhen'
      }
    }
  },
  created: function () {
    // console.log('1 --- created')
  },
  methods: {
    startMix: function () {
      // console.log('1 --- startMix')
    }
  },
  computed: {
    getMsg: function () {
      return 'getMsg 1!!'
    },
    getMsg2: function () {
      return 'getMsg2 1!!'
    },
    getMsg5: function () {
      return 'getMsg5 1!!'
    }
  }
})

Vue.mixin({
  data () {
    return {
      msg: '2-msg',
      msg1: '2-msg-1',
      msg4: '2-msg4',
      numList: [4, 5],
      site: {
        name: 'alibaba',
        hahaList: [3, 4],
        url: 'www.alibaba.com',
        people: 30000
      }
    }
  },
  created: function () {
    // console.log('2 --- created')
  },
  methods: {
    startMix: function () {
      // console.log('2 --- startMix')
    },
    hello: function () {
      // console.log('2 --- hello')
    }
  },
  computed: {
    getMsg: function () {
      return 'getMsg 2!!'
    },
    getMsg2: function () {
      return 'getMsg2 2!!'
    }
  }
})

var localMix3 = {
  data () {
    return {
      msg: '3',
      msg1: '3-msg1',
      msg2: '3-msg2',
      msg3: '3-msg3',
      site: {
        name: '111',
        url: 'www',
        country: 'China'
      }
    }
  },
  created: function () {
    // console.log('3 --- created')
  },
  methods: {
    startMix: function () {
      // console.log('3 --- startMix')
    },
    hello: function () {
      // console.log('3 --- hello')
    }
  },
  computed: {
    getMsg: function () {
      return 'getMsg 3!!'
    },
    getMsg2: function () {
      return 'getMsg2 3!!'
    },
    getMsg3: function () {
      return 'getMsg3 3!!'
    },
    getMsg4: function () {
      return 'getMsg4 3!!'
    }
  }
}
var localMix2 = {
  data () {
    return {
      msg: '2',
      msg1: '2-msg1',
      msg2: '2-msg2',
      site: {
        name: '',
        url: 'www.taobao.com',
        title: 'I am '
      }
    }
  },
  created: function () {
    // console.log('2 --- created')
  },
  methods: {
    startMix: function () {
      // console.log('2 --- startMix')
    },
    hello: function () {
      // console.log('2 --- hello')
    }
  },
  computed: {
    getMsg: function () {
      return 'getMsg 2!!'
    },
    getMsg2: function () {
      return 'getMsg2 2!!'
    },
    getMsg3: function () {
      return 'getMsg3 2!!'
    }
  },
  filters: {
    capitalize3: function (value) {
      if (!value) return ''
      return value + ' local-filter1!!'
    }
  }
}
var localMix1 = {
  data () {
    return {
      msg: '1',
      msg1: '1-msg1',
      numList: [2, 3],
      site: {
        name: '',
        url: 'www.taobao.com',
        hahaList: [3, 4]
      }
    }
  },
  mixins: [localMix2],
  created: function () {
    // console.log('1 --- created')
  },
  methods: {
    startMix: function () {
      // console.log('1 --- startMix')
    },
    hello: function () {
      // console.log('1 --- hello')
    }
  },
  filters: {
    capitalize2: function (value) {
      if (!value) return ''
      return value + ' 1!!'
    }
  },
  computed: {
    getMsg: function () {
      return 'getMsg 1!!'
    },
    getMsg2: function () {
      return 'getMsg2 1!!'
    }
  }
}

export default {
  name: 'basicExtend',
  data () {
    return {
      msg: 'basicExtend',
      numList: [1, 2],
      site: {
        name: '',
        hahaList: [1, 2]
      }
    }
  },
  mixins: [localMix3, localMix1],
  created: function () {
    // console.log('basicExtend --- created')
  },
  methods: {
    startMix: function () {
      this.site.hahaList.push(999)
      this.site.title = ''
      // console.log('basicExtend --- startMix')
    }
  },
  computed: {
    showData: function () {
      return JSON.stringify(this.$data)
    },
    getMsg: function () {
      return this.msg + ' !!!'
    }
  }
}
</script>

<style scoped>
  .form-group {
    width: 600px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    font-size: 16px;
  }
  .form-group > label {
    width: 25%;
    margin-right: 20px;
    color: #666;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .form-group > label.for-input:after {
    content: ":";
  }
  .form-group > .input-item {
    width: 75%;
    color: #666;
    line-height: 32px;
  }
  .form-group .input-item > input {
    width:90%;
    height: 32px;
  }
  .form-group .input-oper {
    color: #009973;
    cursor: pointer;
    margin-right: 10px;
  }
  .form-group .input-item > button {
    background: #3cc;
    padding: 10px 30px;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
  }
</style>



