<template>
  <div>
    <vc-description title="">
      <vc-description-item label="">{{ city_name }}</vc-description-item>
      <vc-description-item :label="''">{{ site_name }}</vc-description-item>
      <vc-description-item :label="''">{{ siteSSSS }}</vc-description-item>
      <vc-description-item :label="''">{{ siteC }}</vc-description-item>
      <vc-description-item :label="''" :span="2">{{ siteCC }}</vc-description-item>
      <vc-description-item :label="''" :labelWrap="false">{{ sitessObj.likes }}</vc-description-item>
      <vc-description-item :label="''" :span="1">{{ sitessObj.text }}</vc-description-item>
    </vc-description>
    <div>
      <vc-button @click="ModSiteName">ModSiteName</vc-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'

export default {
  name: 'module',
  data () {
    return {
      sitessObj: {
        name: '',
        age: 23,
        country: '',
        city: '',
        company: '',
        likes: '',
        address: '',
        text: ''
      },
      handerFn: Function
    }
  },
  created () {
    this.handerFn = this.$store.watch((state, getters) => {
      return getters.city_name + getters.site_name
    }, this.watchCallBack, { deep: false })
  },
  destroyed () {
    this.handerFn()
  },
  computed: {
    ...mapGetters(['city_name', 'site_name']),
    ...mapState({ siteSSSS: state => state.moduleA.site }),
    ...mapState('moduleC', { siteC: state => state.site }),
    ...mapState('moduleC/ccc', { siteCC: state => state.name })
  },
  methods: {
    ModSiteName () {
      this.$store.dispatch('getName')
    },
    watchCallBack () {
      alert('watchCallBack')
    }
  }
}
</script>

<style scoped lang="less">

</style>



